/*==========================================================================
   #RATING PRODUCT
   ========================================================================== */




.c-rating {
  float: left;
  height: 40px;
}

.c-rating:not(:checked)>input {
  position: absolute;
  top: -9999px;
}

.c-rating:not(:checked)>label {
  float: right;
  width: 1em;
  overflow: hidden;
  white-space: nowrap;
  cursor: pointer;
  color: #ccc;

  @include font-size($global-medium-type);
}

.c-rating:not(:checked)>label:before {
  content: '★ ';
}

.c-rating>input:checked~label {
  color: #ffc700;
}

.c-rating:not(:checked)>label:hover,
.c-rating:not(:checked)>label:hover~label {
  color: #deb217;
}

.c-rating>input:checked+label:hover,
.c-rating>input:checked+label:hover~label,
.c-rating>input:checked~label:hover,
.c-rating>input:checked~label:hover~label,
.c-rating>label:hover~input:checked~label {
  color: #c59b08;
}

/* Modified from: https://github.com/mukulkant/Star-rating-using-pure-css */