/*==========================================================================
   #PRICE
   ========================================================================== */





$price-size-normal: $global-large-type;
$price-secondary-size-normal: $price-size-normal - 4;
$currency-size-normal: $price-secondary-size-normal;

$price-size-small: $global-medium-type;
$price-secondary-size-small: $price-size-small - 4;
$currency-size-small: $price-secondary-size-small;

$price-size-huge: $global-huge-type;
$price-secondary-size-huge: $price-size-huge - 4;
$currency-size-huge: $price-secondary-size-huge;

.c-price {
  >[class^="c-price"] {
    display: inline-block;
  }
}


.c-price__price {
  @include font-size($price-size-normal);
  font-weight: $global-type-semi-bold;
  color: $global-orange;
}

// Secondary price (before promotion)

.c-price__price--secondary {
  @include font-size($price-secondary-size-normal);
  color: $global-gray;
  font-weight: $global-type-light;
  text-decoration: line-through;

  @include mq($until: desktop) {
    @include font-size($price-secondary-size-normal - 2);
  }

  .c-price__currency {
    @include font-size($currency-size-normal - 4);

    @include mq($until: desktop) {
      @include font-size($currency-size-normal - 6);
    }
  }
}


// Price elements

.c-price__currency {
  @include font-size($currency-size-normal);

  @include mq($until: desktop) {
    @include font-size($currency-size-normal - 2);
  }
}


// Size variations

.c-price--small {

  .c-price__price {
    @include font-size($price-size-small);

    .c-price__currency {
      @include font-size($currency-size-small);
    }
  }

  .c-price__price--secondary {
    @include font-size($price-secondary-size-small);

    .c-price__currency {
      @include font-size($price-secondary-size-small - 4);
    }
  }
}

.c-price--huge {

  .c-price__price {
    @include font-size($price-size-huge);

    .c-price__currency {
      @include font-size($currency-size-huge);
    }
  }

  .c-price__price--secondary {
    @include font-size($price-secondary-size-huge);

    .c-price__currency {
      @include font-size($price-secondary-size-huge - 4);
    }
  }
}