/* ==========================================================================
   #BUTTONS
   ========================================================================== */

/**
 * This is an example component. Extend inuitcss by building your own components
 * that make up your UI. Component classes are prefixed with a `c-`.
 */

/**
 * 1. Allow us to style box model properties.
 * 2. Line different sized buttons up a little nicer.
 * 3. Make buttons inherit font styles (often necessary when styling `input`s as
 *    buttons).
 * 4. Reset/normalize some styles.
 * 5. Force all button-styled elements to appear clickable.
 */

.c-btn {
  display: inline-block;
  /*[1]*/vertical-align: middle;
  /*[2]*/font: inherit;
  /*[3]*/text-align: center;
  /*[4]*/margin: 0;
  /*[4]*/cursor: pointer;
  /*[5]*/padding: $inuit-global-spacing-unit-small $inuit-global-spacing-unit;
  transition: $global-button-transition;
  border: none;
  border-radius: $global-border-radius;
  -webkit-border-radius: $global-border-radius;
  -moz-border-radius: $global-border-radius;
  -ms-border-radius: $global-border-radius;
  -o-border-radius: $global-border-radius;
  -webkit-transition: $global-button-transition;
  -moz-transition: $global-button-transition;
  -ms-transition: $global-button-transition;
  -o-transition: $global-button-transition;

  @include mq($until: desktop) {
    padding: $unit/4 $unit/2;
  }
}





/* Style variants
   ========================================================================== */

.c-btn--primary {
  background: $global-darkblue;

  @include mq($until: desktop) {
    -webkit-box-shadow: $global-box-shadow;
            box-shadow: $global-box-shadow;
  }

  &,
  &:hover,
  &:active,
  &:focus {
    text-decoration: none; /* [4] */
    color: #fff;
  }
}


.c-btn--secondary {
  background-color: $global-gray;

  &,
  &:hover,
  &:active,
  &:focus {
    text-decoration: none;
    color: #fff;
  }
}

.c-btn--fake {
  cursor: pointer;
  border: none;
  background-color: transparent;
  padding: 0;
}


.c-btn--tertiary {
  background-color: white;
  color: black;
  border: $global-border-style;
}


.c-btn--cta {
  background: $global-orange;

  @include mq($until: desktop) {
    -webkit-box-shadow: $global-box-shadow;
            box-shadow: $global-box-shadow;
  }

  &,
  &:hover,
  &:active,
  &:focus {
    text-decoration: none;
    color: $global-white;
  }
}

.c-btn--attract {
  background-color: $global-red;
  color: $global-white;

  &,
  &:hover,
  &:active,
  &:focus {
    text-decoration: none;
    background-color: $global-less-red;
  }
}


.c-btn--rounded {
  border-radius: $global-button-radius;
  -webkit-border-radius: $global-button-radius;
  -moz-border-radius: $global-button-radius;
  -ms-border-radius: $global-button-radius;
  -o-border-radius: $global-button-radius;
}


.c-btn--type-large {
  @include font-size($global-large-type);

  @include mq($from: desktop) {
    @include font-size($global-small-type);
  }
}


.c-btn--stretch {
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}



/* Size variants
   ========================================================================== */

.c-btn--small {
  padding: $inuit-global-spacing-unit-tiny $inuit-global-spacing-unit-small;
}

.c-btn--large {
  padding: $inuit-global-spacing-unit $inuit-global-spacing-unit-large;
}





/* Ghost buttons
   ========================================================================== */

/**
 * Ghost buttons have see-through backgrounds and are bordered.
 */

$btn-ghost-border-width: 2px;

.c-btn--ghost {
  border: $btn-ghost-border-width solid currentColor;
  padding: ($inuit-global-spacing-unit-small - $btn-ghost-border-width) ($inuit-global-spacing-unit - $btn-ghost-border-width);

  &,
  &:hover,
  &:active,
  &:focus {
    background: none;
  }

  &.c-btn--small {
    padding: ($inuit-global-spacing-unit-tiny - $btn-ghost-border-width) ($inuit-global-spacing-unit-small - $btn-ghost-border-width);
  }

  &.c-btn--large {
    padding: ($inuit-global-spacing-unit - $btn-ghost-border-width) ($inuit-global-spacing-unit-large - $btn-ghost-border-width);
  }

  &.c-btn--primary {
    color: #4a8ec2;

    &:hover,
    &:focus {
      color: #3774a2;
    }

  }

  &.c-btn--secondary {
    color: #2f4054;

    &:hover,
    &:focus {
      color: #1d2733;
    }

  }

  &.c-btn--tertiary {
    color: #fff;

    &:hover,
    &:focus {
      color: #fff;
    }

  }

}
