/*==========================================================================
   #CATEGORY-LIST
   ========================================================================== */




$cat-list-max-width: $global-8-col-width !default;

.c-cat-list {
  display: block;
  max-width: $cat-list-max-width;
  margin: 0 auto;
  padding: $unit/4;

  &--horizontal {
    padding: 0
  }
}

.c-cat-list__item {
  padding: $unit/2;
  padding-right: 0;

  -webkit-transition: opacity 200ms cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: opacity 200ms cubic-bezier(0.645, 0.045, 0.355, 1);
  -moz-transition: opacity 200ms cubic-bezier(0.645, 0.045, 0.355, 1);
  -ms-transition: opacity 200ms cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition: opacity 200ms cubic-bezier(0.645, 0.045, 0.355, 1);

  &:first-child {
    padding-left: $unit;
  }
}

.c-cat-list__item-detail {
  display: block;

  @include font-size($global-small-type);
  font-weight: $global-type-light;
  line-height: $global-golden-ratio;

  >i[class^="fa"],
  >svg {
    display: block;
  }

  >svg {
    margin: auto;
    margin-bottom: $unit/4;
  }
}


// Color theme variations

.c-cat-list--dark {
  background-color: $global-darkblue;
  color: $global-white;

  .c-cat-list__item:hover {
    //background-color: $global-orange;
    opacity: .65;
  }

  i[class^="fa"],
  svg {
    color: $global-white;
  }
}