/* ==========================================================================
   #SPACING
   ========================================================================== */



$spacing-base-size: $unit/2;

$spacing-sizes: ("0": 0,
  "4": 4px,
  "6": $spacing-base-size/2,
  "12": $spacing-base-size,
  "24": $spacing-base-size*2,
  "36": $spacing-base-size*3,
  "48": $spacing-base-size*4,
  "60": $spacing-base-size*5,
  "72": $spacing-base-size*6,
  "120": $spacing-base-size*10,
  "10p": 10%,
  "15p": 15%,
);

$spacing-props: ("m": "margin",
  "p": "padding",
) !default;

$spacing-directions: (null: null,
  "t": "-top",
  "r": "-right",
  "b": "-bottom",
  "l": "-left",
  "h": "-left""-right",
  "v": "-top""-bottom",
);

@each $prop-namespace,
$prop in $spacing-props {

  @each $direction-namespace,
  $directions in $spacing-directions {

    @each $size-namespace,
    $size in $spacing-sizes {

      .u-#{$prop-namespace}#{$direction-namespace}-#{$size-namespace} {

        @each $direction in $directions {
          #{$prop}#{$direction}: $size;
        }
      }
    }
  }
}

/* Margin auto classes */

@each $direction-namespace,
$directions in $spacing-directions {
  .u-m#{$direction-namespace}-auto {

    @each $direction in $directions {
      margin#{$direction}: auto;
    }
  }
}

.u-m-center {
  margin: 0 auto;
}