/* ==========================================================================
   #SIMPLE CAROUSEL
   ========================================================================== */




$carousel-spacing: $unit;
$carousel-small-spacing: $carousel-spacing/2;
$carousel-tiny-spacing: $carousel-spacing/6;

.o-carousel {
  @include inuit-clearfix;
  margin-left: -$carousel-spacing;

  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;

  // make scroll bar available
  overflow: auto hidden;
  white-space: nowrap;
}

.o-carousel__item {
  display: inline-block;
  padding-left: $carousel-spacing;
  overflow-wrap: break-word;
  white-space: initial;

  padding-left: $carousel-spacing;

  // default maximum columns is 4
  // -webkit-box-flex: 0;
  // -ms-flex: 0 0 25%;
  // flex: 0 0 25%;
  // max-width: 25%;

  &:focus {
    outline: none;
  }

  &.slick-slide {
    padding-bottom: $unit/2;
    padding-top: $unit/2;
  }
}


// Size variations

$carousel-columns: 4 5 6 8 10 12 !default;

@each $column in $carousel-columns {

  .o-carousel--#{$column}col {

    .o-carousel__item {
      -webkit-box-flex: 0;
      -ms-flex: 0 0 (1/$column) * 100%;
      flex: 0 0 (1/$column) * 100%;
      max-width: (1/$column) * 100%;
    }
  }
}


// Gutter size variations

.o-carousel--tiny {
  margin-left: -$carousel-tiny-spacing;

  >.o-carousel__item {
    padding-left: $carousel-tiny-spacing;
  }
}

.o-carousel--small {
  margin-left: -$carousel-small-spacing;

  >.o-carousel__item {
    padding-left: $carousel-small-spacing;
  }
}