/* ==========================================================================
   INUITCSS
   ========================================================================== */


/**
 * CONTENTS
 *
 * SETTINGS
 * Config...............Project-level configuration and feature switches.
 * Core.................inuitcss’ core and setup settings.
 * Global...............Project-wide variables and settings.
 *
 *
 * TOOLS
 * Font-size............A mixin which guarantees baseline-friendly line-heights.
 * Clearfix.............Micro clearfix mixin.
 * Hidden...............Mixin for hiding elements.
 * Sass MQ..............inuitcss’ default media query manager.
 * Position.............Mixin for positioing elements (center, bottom, etc).
 * Text.................
 *
 * GENERIC
 * Box-sizing...........Better default `box-sizing`.
 * Normalize.css........A level playing field using @necolas’ Normalize.css.
 * Reset................A tiny reset to complement Normalize.css.
 * Shared...............Sensibly and tersely share some global commonalities
 *                      (particularly useful when managing vertical rhythm).
 *
 * ELEMENTS
 * Page.................Set up our document’s default `font-size` and
 *                      `line-height`.
 * Headings.............Very minimal (i.e. only font-size information) for
 *                      headings 1 through 6.
 * Images...............Base image styles.
 * Tables...............Simple table styles.
 * Icon.................i tag for icon
 * Input................Input field styles.
 * Anchor...............Hyperlink styles.
 * Divider..............Page divider styles.
 *
 *
 * OBJECTS
 * Wrapper..............Page constraint object.
 * Layout...............Generic layout module.
 * Media................Image- and text-like content side by side. The
 *                      poster-child of OOCSS.
 * Flag.................Table-layout-based advancement on the Media object.
 * List-bare............Lists with no bullets or indents.
 * List-inline..........A list whose items all site in a line.
 * Box..................Simple boxing abstraction.
 * Block................Image-on-top-of-text object.
 * Ratio................A container for maintaining aspect ratio of content.
 * Crop.................Provide a cropping context for media (images, etc.).
 * Table................Classes for manipulating `table`s.
 * Pack.................Pack items into available horizontal space.
 * Carousel.............Simple carousel-like with a scroll bar.
 * Column...............Column like grid.
 *
 *
 * COMPONENTS
 * Avatar...............User profile picture styles.
 * Buttons..............Button variations.
 * Bundles..............Product bundles
 * Breadcrumb...........Link to a specific category/product.
 * Category List........The category list in horitzontal display or vertical display.
 * Footer...............Page footer.
 * Header...............Page header, includes searchbar, logo and cart.
 * Navigation...........Page navigation.
 * Price................Product price, currency, price before sale.
 * Product..............Product's name, image, date, price, etc.
 * Product Option.......Option in product page with its variation.
 * Search Bar...........Page search bar.
 * Section..............Page section, each section may be separated by some space.
 * Slider...............Fix slick-slider UI issues.
 * Rating...............Star rating (beta).
 * Tab..................Tab like component, control using JS.
 *
 *
 * UTILITIES
 * Clearfix.............Bind our clearfix onto a utility class.
 * Widths...............Simple width helper classes.
 * Headings.............Reassigning our heading styles to helper classes.
 * Spacings.............Nudge bits of the DOM around with these spacing
 *                      classes.
 * Responsive-Spacings..Enhances the function of normal spacings for
 *                      responsive usage.
 * Print................Reset-like styles taken from the HTML5 Boilerplate.
 * Hide.................Helper classes to hide content
 * Border...............Give element different styles of border.
 * Background...........Set element background color.
 * Display..............Simply set the display style of the element.
 * Event................DOM event listener styles.
 * Flex.................Flex's properties variations.
 * Position.............Position related styles (absolute, fixed, float).
 * Responsive Display...Enhances the function of normal display for
                        responsive usage.
 * Text.................Text color, alignment, font-size, etc.
 * Width................Set a specific width for element.
 * Layer................Layer priority, i.e z-index.
 *
 *
 * TRUMP
 * Product..............Special product type styles.
 */




