/* ==========================================================================
   #ICON
   ========================================================================== */




i {
  line-height: 0;
  color: $global-darkblue;
}


//

i.rect {
  line-height: 1 !important;
}

// Size variations

i.tiny {
  @include mq($from: mobile) {
    @include font-size($global-smaller-type);
  }
  @include mq($from: tablet) {
    @include font-size($global-smaller-type + 2);
  }
}

i.small {
  @include mq($from: mobile) {
    @include font-size($global-small-type);
  }
  @include mq($from: tablet) {
    @include font-size($global-small-type + 2);
  }
}

i.medium,
svg.medium {
  @include mq($from: mobile) {
    @include font-size($global-medium-type);
  }
  @include mq($from: tablet) {
    @include font-size($global-medium-type + 2);
  }
}

i.large,
svg.large {
  @include mq($from: mobile) {
    @include font-size($global-larger-type);
  }

  @include mq($from: tablet) {
    @include font-size($global-larger-type + 2);
  }
}

i.huge {
  @include mq($from: desktop) {
    @include font-size($global-huge-type, $global-golden-ratio);
  }
}




// Color theme variations

i.inverted {
  color: $global-white;
}
