/*==========================================================================
   #TAB
   ========================================================================== */




$tab-header-spacing: $unit;


.c-tab {
  display: block;
}

.c-tab__header {
  display: block;
  font-weight: $global-type-bold;
}

.c-tab__header-name {
  display: inline-block;
  opacity: .35;

  &:not(:first-child) {
    margin-left: $tab-header-spacing;
  }


  &.active {
    border-bottom: 3px solid $global-orange;
    opacity: 1;
  }
}

.c-tab__content {
  margin-top: $unit/2;

  @include mq($until: desktop) {
    padding-left: $unit/4;
  }
}

