/*==========================================================================
   #HERO BANNER
   ========================================================================== */




$hero-dots-spacing: $unit/2;


.c-hero {
  margin: 0 auto;
}

.c-hero__wrapper {

  /* Override default position of slick slider */
  &.slick-slider {
    position: relative;
  }
}

.c-hero__dots {

  &.slick-dots {
    text-align: left;

    [role="presentation"] {
      margin-left: 12px;
      margin-right: 0;
  
      &:first-child {
        margin-left: 15%;
      }
    }
  }
}

.c-hero__banner {
  position: relative;
}

.c-hero__banner-img {
  width: 100vw;
}

.c-hero__text {
  position: absolute;
  top: 0;

  @include mq($from: desktop) {
    left: 15%;
  }
}

.c-hero__cta {
  width: 33.333334%;

  @include mq($from: desktop) {
    width: 15%;
  }
  
}