/*==========================================================================
   #PRODUCT-SPECIAL
  ==========================================================================*/




.t-currency {
  @include font-size($global-small-type);

  @include mq($from: desktop) {
    @include font-size(14px);
  }
}


.t-price {
  color: $global-orange;
  font-weight: $global-type-semi-bold;
}

.t-price--before {

  color: $global-gray;
  text-decoration: line-through;

  @include mq($until: mobile) {
    display: none;
  }

  @include font-size($global-medium-type);
}


.t-price--huge {

  .t-price--before {
    @include font-size($global-large-type);
  }

  .t-currency {

    @include font-size($global-large-type);

    @include mq($from: desktop) {
      @include font-size($global-larger-type);
    }
  }
}

