/* ==========================================================================
   #PRODUCT
   ========================================================================== */




.c-product {
  margin-top: $unit/2;
  cursor: pointer;

  /*
  .c-product__img {
    @include mq($until: desktop) {
      -webkit-box-shadow: $global-box-shadow;
      box-shadow: $global-box-shadow;
    }
  }
  */

  &:hover {

    .c-product__discount,
    .c-product__date {
      visibility: visible;
      opacity: 1;
    }
  }
}

.c-product--hover {
  &:hover {
    box-shadow: $global-box-shadow;
  }
}


.c-product__name {
  margin-top: $unit/2;
  font-size: $global-nice-type;
  font-weight: $global-type-medium;

  // overflow: hidden;
  // white-space: nowrap;
  // text-overflow: ellipsis;

  @include truncate-word();

  @include mq($from: desktop) {
    font-size: $global-nice-type;
    line-height: 1.42861;
  }
}


.c-product__img {
  position: relative;


  &.c-product__img--bordered {
    border: $global-border-style;
  }

  >img {
    width: auto;
    height: auto;

    @include mq($until: tablet) {
      height: 100px; 
    }
    @include mq($from: desktop) {
      height: $global-slider-height-desktop;
    }
  }

  /* Quick view butotn */
  .c-btn {
    opacity: 0;
  }

  &:hover {
    .c-btn {
      opacity: 1;
      transition: $global-opacity-transition;
      -webkit-transition: $global-opacity-transition;
      -moz-transition: $global-opacity-transition;
      -ms-transition: $global-opacity-transition;
      -o-transition: $global-opacity-transition;
    }
  }
}


.c-product__rating {
  line-height: 1;

  >span {
    @include mq($until: desktop) {
      display: block
    }
  }
}


.c-product__discount,
.c-product__date {
  position: absolute;
  left: 0;
  top: 0;

  // default hide on desktop
  @include mq($from: desktop) {
    visibility: hidden;
    opacity: 0;
    transition: $global-opacity-transition;
    -webkit-transition: $global-opacity-transition;
    -moz-transition: $global-opacity-transition;
    -ms-transition: $global-opacity-transition;
    -o-transition: $global-opacity-transition;
  }

  background-color: $global-orange;
  color: white;

  @include mq($from: mobile) {
    padding: 2px 4px;
    @include font-size(10px);
  }

  @include mq($from: tablet) {
    padding: $unit/4 $unit/2;
    @include font-size(14px);
  }
}




/* Direction variations */

.c-product--horizontal {
  margin-top: $unit/2;

  .c-product__name {
    margin-top: 0;
  }
}




/* Position variations */

.c-product--secondary {
  margin-top: $unit/2;

  .c-product__name {

    @include mq($from: desktop) {
      font-size: $global-small-type;
      line-height: 1.25;
      margin-top: 0;
    }
  }

  .c-product__img {
    @include mq($from: desktop) {
      height: auto;
    }
  }

  .c-product__rating {
    [class^="fa"] {
      font-size: $global-smaller-type;
    }
  }
}