/*==========================================================================
   #PAGING
   ========================================================================== */


$paging-spacing: $unit/2;


.c-paging {
  display: flex;
  align-items: center;
  justify-content: center;
}


.c-paging-page {
  @include font-size($global-nice-type);
  display: inline-block;
  margin-left: $paging-spacing;
  margin-right: $paging-spacing;
  color: black;
  padding: $paging-spacing/4 $paging-spacing;
  border-radius: $global-round-radius;
  -webkit-border-radius: $global-round-radius;
  -moz-border-radius: $global-round-radius;
  -ms-border-radius: $global-round-radius;
  -o-border-radius: $global-round-radius;
  background-color: $global-blur-gray;

  &--current {
    background-color: $global-darkblue;
    color: $global-white;
  }
}

.c-paging-next,
.c-paging-prev {
  display: inline-block;
}

.c-paging-next {
  margin-left: $unit;
}

.c-paging-prev {
  margin-right: $unit;
}