/*==========================================================================
   #PROGRESS
   ========================================================================== */




.c-progress {
  white-space: nowrap;
}

.c-progress__item {
  margin-bottom: $unit/2;
  display: flex;

  >* {
    display: inline-block;
  }
}

.c-progress__graph,
.c-progress__sum {
  display: inline-block;
  vertical-align: top;
}

.c-progress__bar {
  width: 70%;
}

.c-progress__label {
  @include font-size($global-small-type, 1);
  margin-right: $unit;
}

.c-progress__value {
  @include font-size($global-small-type, 1);
  margin-left: $unit;
}

.c-progress__sum {
  text-align: center;
}

.c-progress__sum-overall {
  @include font-size($global-huge-type);
  color: $global-white;
  font-weight: $global-type-bold;
  letter-spacing: 1px;
  background-color: $global-darkblue;
  padding: $unit*2 $unit;
  margin-bottom: $unit/2;

  border-radius: $global-round-radius;
  -webkit-border-radius: $global-round-radius;
  -moz-border-radius: $global-round-radius;
  -ms-border-radius: $global-round-radius;
  -o-border-radius: $global-round-radius;

  -webkit-box-shadow: 0px 12px 13px -7px rgba(35, 47, 62, 0.71);
  -moz-box-shadow: 0px 12px 13px -7px rgba(35, 47, 62, 0.71);
  box-shadow: 0px 12px 13px -7px rgba(35, 47, 62, 0.71);
}

.c-progress__sum-count {
  @include font-size($global-small-type);
}