/*==========================================================================
   #BREADCRUMB
   ========================================================================== */



.c-breadcrumb {
  @include mq($from: desktop) {
    margin-bottom: $unit/2;
  }

  @include font-size($global-small-type);
  font-weight: $global-type-light;
  color: $global-gray;
  margin-bottom: $unit/4;
}

.c-breadcrumb__child:not(:first-child) {
  padding-left: $unit/12;
}

.c-breadcrumb__child:not(:last-child) {
  &:after {
    content: ' › ';
  }
}