/* ==========================================================================
   #SECTION
   ========================================================================== */



$section-spacing: $unit/2;
.c-section {
  @include inuit-clearfix();
  background-color: $global-white;
  overflow: hidden;
  margin-bottom: $unit;
  position: relative;
}

.c-section__title {
  //margin-top: $section-spacing;

  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: baseline;
  -ms-flex-align: baseline;
  align-items: baseline;

  @include font-size($global-larger-type);
  font-weight: 700;

  @include mq($until: desktop) {
    padding-left: $section-spacing/2;
    margin-bottom: $section-spacing;
  }

  &--no-margin {
    margin-top: 0;
  }
}

.c-section__sub-title {
  @include font-size($global-small-type);
  color: $global-gray;
  margin-bottom: $section-spacing;
  
  @include mq($until: desktop) {
    padding-left: $section-spacing/2;
  }
}

.c-section__content {
  overflow: hidden;
  width: 100%;

  @include mq($from: desktop) {
    margin-top: $section-spacing;
  }
}

.c-section__item {
  background-color: $global-white;

  &.ads {
    background-color: transparent;
    padding: 0;
  }
}

.c-section__dots {
  position: absolute;
  right: 0;
  top: 0;

  &.slick-dots {
    width: auto;
    text-align: right;
  }
}

// Style variations

.c-section--transparent {
  background-color: transparent;
}

.c-section--splitted {
  margin-bottom: $section-spacing*2;

  @include mq($until: desktop) {
    margin-bottom: $section-spacing;
  }
}


// Speicific sections spacing

.c-section[data-section="Category List"] {
  margin-bottom: $section-spacing;
}