/* ==========================================================================
   #HEADER
   ========================================================================== */




$header-max-width: $inuit-wrapper-width !default;

$header-tool-font-size: $global-smaller-type !default;
$header-topnav-font-size: $global-small-type !default;
$header-topnav-font-size-mobile: $global-tiny-type !default;

$header-searchbar-height: 50px;
$header-searchbar-height-mobile: 36px;

.c-header {
  @include inuit-clearfix();

  @include mq($from: desktop) {
    /* padding: $unit; */
    padding: 0;
    line-height: 1;
    background-color: inherit;
  }
}

/*
.c-header__banner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin: $unit auto;
}
*/


// Top navigation bar

.c-header__topnav {
  background-color: $global-darkblue;
}

.c-header__topnav-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;

  margin: 0 auto;
  max-width: $header-max-width;

  list-style: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.c-header__topnav-item {
  display: inline-block;
  @include font-size($header-topnav-font-size-mobile + 2, 1);
  color: $global-white;
  padding: $unit/4;

  transition: $global-opacity-transition;
  -webkit-transition: $global-opacity-transition;
  -moz-transition: $global-opacity-transition;
  -ms-transition: $global-opacity-transition;
  -o-transition: $global-opacity-transition;

  &:hover {
    opacity: .65;
  }

  @include mq($from: desktop) {
    @include font-size($header-topnav-font-size);
    padding: $unit/3;
  }
}


// Main navigation

.c-header__nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  @include mq($until: desktop) {
    padding: $unit;
    flex-wrap: wrap;
  }

  @include mq($from: desktop) {
    // overflow: hidden;
    max-width: $header-max-width;
    margin: $unit*2 auto;
  }
}

.c-header__nav-bars {
  text-align: left;
}

.c-header__nav-logo {
  padding-left: 0;
  /*fix the redundant padding from o-layout__item*/

  >[href="#"] {
    line-height: 0;
    display: block;
  }

  @include mq($until: desktop) {
    text-align: center;
  }
}

.c-header__nav-logo-img {
  width: $unit;

  @include mq($from: desktop) {
    width: $unit*1.5;
  }
}

.c-header__nav-search {
  [type="search"] {
    height: $header-searchbar-height;

    @include mq($until: desktop) {
      height: $header-searchbar-height-mobile;
    }
  }
}

.c-header__nav-tool {
  position: relative;
  @include font-size($header-tool-font-size);
  text-align: right;

  [class^="fa"],
  .svg-inline--fa {
    margin-right: $unit/3;

    @include mq($until: desktop) {
      margin-right: 0;
    }
  }

  [data-display="inline-flex"] {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
  }

  >* {
    margin-right: $unit;

    &:nth-child(n-1) {
      margin-right: 0;
    }
  }
}

.c-header__cart {
  position: relative;

  >[class^="fa"] {
    position: relative;
  }

  &:hover {
    + .cart-view {
      display: block;
    }

    .cart-caret {
      display: block;
    }
  }
}

.cart-caret {
  display: block;
  left: 0;
  overflow: hidden;
  position: absolute;
  bottom: -10px;
  width: 100%;
  height: 8px;
  z-index: 1;

  &:after {
    content: "";
    display: block;
    position: absolute;
    top: -0.5px;
    left: 50%;
    width: 10px;
    height: 10px;

    transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    -webkit-transform-origin: 0% 0;
    transform-origin: 0% 0;
    border-radius: 2px 0 0 0;
    -webkit-border-radius: 2px 0 0 0;
    -moz-border-radius: 2px 0 0 0;
    -ms-border-radius: 2px 0 0 0;
    -o-border-radius: 2px 0 0 0;
    background-color: $global-white;
    border: 1px solid;
    border-top-color: currentcolor;
    border-right-color: currentcolor;
    border-bottom-color: currentcolor;
    border-left-color: currentcolor;
    border-color: #d2d2d7;
  }
}

.cart-view {
  display: block;
  right: 0;
  position: absolute;
  top: 36px;
  z-index: 1;
}

.cart-view__content {
  overflow-y: auto;
  padding: $unit/4 $unit/2;
  background: $global-white;
  border: $global-border-style;
  border-radius: $global-border-radius;
  -webkit-border-radius: $global-border-radius;
  -moz-border-radius: $global-border-radius;
  -ms-border-radius: $global-border-radius;
  -o-border-radius: $global-border-radius;
  height: 240px;
}

.cart-product {
  &:not(:last-child) {
    border-bottom: $global-border-style;
  }
}

// Category list
[data-section="Category List"][data-section="Category List"] {
  background-color: $global-darkblue;
  max-width: $global-10-col-width;
  margin: 0 auto;
}