/* ==========================================================================
   #NAVIGATION
   ========================================================================== */




.c-nav {

  &.c-nav--multi-level {
    padding-left: 0;
    padding-right: 0;

    position: relative;

    .child {

      &:not(.active) {
        visibility: hidden;
      }

      position: absolute;
      top: 0;

      width:100%;

      z-index: $global-first-z-index;
      background-color: $global-white;
      border-left: 1px solid $global-border-color;
    }

  }
}

.c-nav__wrapper {
  list-style: none;
  margin-left: 0;

  @include mq($from: desktop) {
    padding-top: $unit/2;
    padding-bottom: $unit/2;
  }
}

.c-nav__item {
  @include mq($from: desktop) {
    margin-bottom: 8px;
    margin-left: 0;
    padding-left: $unit;
  }
}