/*==========================================================================
   #TABLE
   ========================================================================== */


.c-table {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  border: $global-border-style;
}

.c-table__row {
  @include font-size($global-small-type);
  background-color: $global-white;
  padding: $unit/2 $unit/4;
  white-space: nowrap;

  &:nth-child(odd) {
    background-color: $global-border-color;
  }
}

.c-table__row-col {
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
}

.c-table__attr {
  font-weight: $global-type-semi-bold;
  width: 60%;
}

.c-table__value {
  width: 40%;
}