/* ==========================================================================
   #BORDER
   ========================================================================== */




$border-styles: (
  m-dark: 0.5px solid $global-darkblue,
  m-blur: 0.5px solid $global-blur-gray,
  m-bright: 0.5px solid $global-orange, 
);

@include generate-class($border-styles, border, u, border-bottom);

.u-border-all-blur {
  border: .5px solid $global-blur-gray;
}