/* ==========================================================================
   #COLUMN
   ========================================================================== */

/**
 * Divide content into separated columns. This objeet give the ability to
 * make the space between columns stays the same regardless the width
 * of each column.
 *
 * Moreover, you can use the scroll modifier to enable overflow scrolling.
 */


.o-col {
  display: table;
  //margin-left: -$unit;
}


.o-col__item {

  display: table-cell;
  //padding-left: $unit;

  &.o-col__separator {
    width: 10%;
  }
}


// Size variations

.o-col--flush {
  // margin-left: 0;

  // >.o-col__item {
  //   padding-left: 0;
  // }

  // >.o-col__separator {
  //   width: 5%;
  // }
  border-spacing: 0;
}

.o-col--tiny {
  border-spacing: $unit/4 0;
}

.o-col--small {
  // margin-left: -$unit/2;

  // >.o-col__item {
  //   padding-left: $unit/2;
  // }
  border-spacing: $unit/2 0;
}

.o-col--medium {
  // margin-left: -$unit;

  // >.o-col__item {
  //   padding-left: $unit;
  // }
  border-spacing: $unit 0;
}


// Centering

.o-col--center {
  margin: 0 auto;
}


// Enable overflow scroll

.o-col--scrollable {
  display: block;
  overflow: auto;

  .o-col__item {
    white-space: nowrap;
    overflow: hidden;
  }
}