/* ==========================================================================
   #INPUT
   ========================================================================== */




$input-padding: $unit/4 !default;

/**
* 1.Override default outline of input active state
*/

input {
  font-family: $global-type;

  &:focus {
    outline: none;
    /*[1]*/
  }
}

input,
select {
  padding: 4px;
}

[type="search"],
[type="text"],
[type="password"] {
  background-color: $global-white;
  border-radius: $global-border-radius;
  -webkit-border-radius: $global-border-radius;
  -moz-border-radius: $global-border-radius;
  -ms-border-radius: $global-border-radius;
  -o-border-radius: $global-border-radius;

  &[data-border="rounded"] {
    border-radius: $global-button-radius;
    -webkit-border-radius: $global-button-radius;
    -moz-border-radius: $global-button-radius;
    -ms-border-radius: $global-button-radius;
    -o-border-radius: $global-button-radius;
  }
}

[type="text"],
[type="search"],
[type="number"],
[type="password"],
select,
textarea {
  border: $global-border-style;
  padding: $input-padding;

  @include mq($from: desktop) {
    padding: $input-padding $input-padding $input-padding $input-padding*2;
  }
}

[type="checkbox"] {
  opacity: .35;

  +label {
    transition: $global-opacity-transition;
    -webkit-transition: $global-opacity-transition;
    -moz-transition: $global-opacity-transition;
    -ms-transition: $global-opacity-transition;
    -o-transition: $global-opacity-transition;

    opacity: .35;
    font-weight: $global-type-light;
  }

  &:checked {
    opacity: 1;

    +label {
      opacity: 1;
    }
  }
}

[type="radio"] {
  +label {
    margin-left: $unit/4;
  }
}

textarea {
  resize: none;
}


/* Pseudo elements */
::placeholder {
  color: $global-gray;
  opacity: 1; /* Firefox */
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: $global-gray;
}
::-moz-placeholder { /* Firefox 19+ */
  color: $global-gray;
}
:-ms-input-placeholder { /* IE 10+ */
  color: $global-gray;
}
:-moz-placeholder { /* Firefox 18- */
  color: $global-gray;
}