/*==========================================================================
   #PRODUCT BUNDLE
   ========================================================================== */

@include mq($until: desktop) {
  .c-bundle {
    list-style: none;
  }


  .c-bundle__item {
    &:after {
      content: "+";
      margin: 0 $unit/2 0 10px;

      @include mq($from: desktop) {
        margin: 0 $unit/4;
      }

      font-size: $global-large-type;
      font-weight: $global-type-bold;
    }

    &:last-child:after {
      content: "";
      margin: 0;
    }
  }
}


.c-bundle {
  list-style: none;
  overflow: auto;
  white-space: nowrap;
}

.c-bundle__product {
  display: inline;
  position: relative;
  margin-right: $unit;

  @include mq($until: desktop) {
    margin-right: $unit/2;
  }

  &:last-child {
    margin-right: 0;
  }

  &--disabled {
    opacity: .35;
  }
}

.c-bundle__img {
  vertical-align: middle;
  border: $global-border-style;
  padding: $unit/4;
  width: 25%;

  @include mq($until: desktop) {
    padding: 2px;
  }
}

.c-bundle__separator {
  position: absolute;
  top: 0;

  @include mq($until: desktop) {
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
  }

  @include mq($from: desktop) {
    margin: 0;
  }

  font-size: $global-large-type;
  font-weight: $global-type-bold;
}