/* ==========================================================================
   #PROGRESS BAR
   ========================================================================== */





progress {
  color: $global-border-color;
  border-radius: $global-button-radius;
  -webkit-border-radius: $global-button-radius;
  -moz-border-radius: $global-button-radius;
  -ms-border-radius: $global-button-radius;
  -o-border-radius: $global-button-radius;
}


/* Webkit support */

progress::-webkit-progress-value {
  border-radius: $global-button-radius;
  -webkit-border-radius: $global-button-radius;
  -moz-border-radius: $global-button-radius;
  -ms-border-radius: $global-button-radius;
  -o-border-radius: $global-button-radius;
  background: $global-gray;
}

progress::-webkit-progress-bar {
  border-radius: $global-button-radius;
  -webkit-border-radius: $global-button-radius;
  -moz-border-radius: $global-button-radius;
  -ms-border-radius: $global-button-radius;
  -o-border-radius: $global-button-radius;
  background: $global-border-color;
}

/* Firefox */

progress::-moz-progress-bar {
  border-radius: $global-button-radius;
  background: $global-gray;
  -webkit-border-radius: $global-button-radius;
  -moz-border-radius: $global-button-radius;
  -ms-border-radius: $global-button-radius;
  -o-border-radius: $global-button-radius;
}

