/* ==========================================================================
   #FOOTER
   ========================================================================== */




$footer-spacing: $unit/2;


.c-footer {
  background-color: $global-darkblue;
  color: $global-white;
}

.c-footer__content {
  padding: $footer-spacing*3;
}

.c-footer__nav {
  overflow: hidden;
}

.c-footer__nav-item {
  margin-bottom: $footer-spacing/2;

  &.parent {
    @include font-size($global-medium-type);
    font-weight: $global-type-bold;
    margin-bottom: $footer-spacing;
  }

  &.child {
    @include font-size($global-nice-type, 17px);
    font-weight: $global-type-light;
  }
}

.c-footer__bottom {
  background-color: $global-darkestblue;

  @include mq($from: mobile) {
    text-align: center;
  }
}

.c-footer__copyright {
  padding: 4px 0;
}