/*==========================================================================
   #PRODUCT OPTION
   ========================================================================== */




.c-option {
  display: block;
  margin-bottom: $unit/2;

  .c-option__img {
    @include mq($until: tablet) {
      //width: 42px;
      height: auto;
    }
  }
}


.c-option__detail {
  @include font-size($global-medium-type);
  color: $global-gray;
  font-weight: $global-type-light;
  display: block;
  margin-bottom: $unit/6;

  @include mq($from: desktop) {
    @include font-size($global-small-type);
    margin-bottom: $unit/2;
  }

  .c-option__value {
    font-weight: $global-type-bold;
    color: $global-darkblue;
  }
}


.c-option__item {
  display: inline-block;

  &:focus {

    .c-option__img {
      border: 1px solid $global-orange;
    }
  }
}


// Option type variations

.c-option--bordered {

  .c-option__img {
    border: $global-border-style;
    border-radius: $global-border-radius;
    -webkit-border-radius: $global-border-radius;
    -moz-border-radius: $global-border-radius;
    -ms-border-radius: $global-border-radius;
    -o-border-radius: $global-border-radius;
  }

  .c-option__control>[data-active="active"] {

    .c-option__img {
      border-color: $global-darkblue;
    }
  }
}


.c-option--rounded {

  .c-option__img {
    border-radius: $global-round-radius;
    -webkit-border-radius: $global-round-radius;
    -moz-border-radius: $global-round-radius;
    -ms-border-radius: $global-round-radius;
    -o-border-radius: $global-round-radius;
  }
}


.c-option--select {

  select {
    @include font-size($global-medium-type);

    @include mq($from: desktop) {
      @include font-size($global-smaller-type);
    }
  }
}


.c-option--control {

  input[type="number"] {
    margin: 0;
    max-width: 60px;

    @include font-size($global-medium-type);

    @include mq($from: desktop) {
      @include font-size($global-smaller-type);
      max-width: 54px;

      border: 0.5px solid $global-border-color;
      border-radius: $global-border-radius;
      -webkit-border-radius: $global-border-radius;
      -moz-border-radius: $global-border-radius;
      -ms-border-radius: $global-border-radius;
      -o-border-radius: $global-border-radius;
    }
  }
}



.c-option__board {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.c-option__btn {
  padding: $unit/6 $unit/2;
  cursor: pointer;

  @extend .u-txt--large;
}