/* ==========================================================================
   #TEXT
   ========================================================================== */


.u-txt {
  @include font-size(1px);
}

// Text size variations

/**
* Classes generated by this mixin will responsively increase or decrease
* its font size according to the media breakpoint. Use the text size 
* classes below if you want a fixed font size.
*/
$type-size: (tiny: $global-tiny-type,
  smaller: $global-smaller-type,
  small: $global-small-type,
  medium: $global-medium-type,
  large: $global-large-type,
  larger: $global-larger-type,
  pretty: $global-nice-type,
  title: $global-title-type,
  huge: $global-huge-type) !default;

$text-breakpoints: (mobile: 320px,
  desktop: 980px,
) !default;

@each $size-name,
$size-value in $type-size {

  .u-txt--#{$size-name} {

    @each $bp-name,
    $bp-value in $text-breakpoints {

      @include mq($from: $bp-name) {
        @include font-size($size-value);
      }

      $size-value: $size-value+2;

    }
  }
}

// Fixed text size variations

$fixed-font-size: 8px 10px 12px 14px 16px 20px 22px 24px 40px !default;

@each $font-size in $fixed-font-size {
  $size: strip-unit($font-size);

  .u-txt-#{$size} {
    @include font-size($font-size);
  }
}


// Text color variations

$type-color: (white: $global-white,
  blur: $global-gray,
  dark: $global-darkblue,
  bright: $global-orange,
);

@include generate-class($type-color, txt, u, color);

// Text alignment

.u-txt-align-center {
  text-align: center;
}

.u-txt-align-right {
  text-align: right;
}

.u-txt-align-justify {
  text-align: justify;
}

.u-txt-align-left {
  text-align: left;
}

// Text weight variations

$type-weight: (hairline: $global-type-hairline,
  xlight: $global-type-xlight,
  light: $global-type-light,
  normal: $global-type-normal,
  w-medium: $global-type-medium,
  semi-bold: $global-type-semi-bold,
  bold: $global-type-bold,
  xbold: $global-type-xbold,
  heavy: $global-type-heavy,
  xheavy: $global-type-xheavy,
);

@include generate-class($type-weight, txt, u, font-weight);

// Text decoration variations

.u-txt-linethrough {
  text-decoration: line-through;
}

.u-txt-underline {
  text-decoration: underline;
}

// Font style variations

.u-txt--i {
  font-style: italic;
}


// Word truncate

$lines-to-truncate: 1 2 3 4 !default;

@each $line in $lines-to-truncate {

  .u-txt-truncate-#{$line} {
    @include truncate-word($line-to-truncate: $line);
  }
}

// Line-hegiht

.u-txt-lineh-1 {
  line-height: 1;
}