///* ========================================================================
//   #GLOBAL
//   ======================================================================== */

// The global settings file contains any project-wide variables; things that
// need to be made available to the entire codebase.




// Widths

$global-10-col-width: 1198px !default;
$global-8-col-width: 959px !default;
$global-col-width: 109px !default;
$global-gutter-width: 12px !default;
$global-slider-height-desktop: 160px !default;


// Color variations

$global-red: #ff424f !default;
$global-less-red: #ff424f !default;
$global-orange: #fd6600 !default;
$global-less-orange: #fd6600 !default;
$global-white: #fff !default;
$global-gray: #999 !default;
$global-blur-gray: rgba(153, 153, 153, 0.3);
$global-darkblue: #232f3e !default;
$global-darkestblue: #131a22 !default;




// Border

$global-border-color: #dfe1e5 !default;
$global-border-radius: 3px !default;
$global-round-radius: 100% !default;
$global-button-radius: 36px !default;
$global-border-style: 0.5px solid $global-border-color !default;




// Button

$global-box-shadow: 0 3px 6px #b8babd, 0 3px 6px $global-white !default;





// Transition

$global-button-transition: all 300ms ease-in-out !default;
$global-opacity-transition: opacity 300ms cubic-bezier(0.645, 0.045, 0.355, 1);




// Type-face

$global-type: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif !default;


$global-tiny-type: 8px !default;
$global-smaller-type: 10px !default;
$global-small-type: 12px !default;
$global-nice-type: 14px !default;
$global-medium-type: 16px !default;
$global-large-type: 20px !default;
$global-larger-type: 22px !default;
$global-title-type: 26px !default;
$global-huge-type: 40px !default;




$global-type-hairline: 100 !default;
$global-type-xlight: 200 !default;
$global-type-light: 300 !default;
$global-type-normal: 400 !default;
$global-type-medium: 500 !default;
$global-type-semi-bold: 600 !default;
$global-type-bold: 700 !default;
$global-type-xbold: 800 !default;
$global-type-heavy: 900 !default;
$global-type-xheavy: 950 !default;




// Gradient

$global-gradient-orange: linear-gradient(90deg, rgba(252,74,26,1) 0%, rgba(247,183,51,1) 100%) !default;
$global-gradient-darkblue: radial-gradient(circle, rgba(20,30,48,1) 0%, rgba(36,59,85,1) 100%) !default;





// Opacity

$global-blur-opacity: .65;





// Constants

$global-golden-ratio: 1.61803398875;
$global-last-z-index: -1 !default;
$global-between-z-index: 999 !default;
$global-first-z-index: 1000 !default;

