/* ==========================================================================
   #POSITION
   ========================================================================== */




// Positioning types

.u-pos-relative {
  position: relative;
}

.u-pos-absolute {
  position: absolute;
}

// Positioning element

.u-pos-right-100 {
  right: 100%;
}

.u-pos-right-0 {
  right: 0;
}

.u-pos-left-100 {
  left: 100%;
}
.u-pos-left-0 {
  left: 0;
}

.u-pos-bot-0 {
  bottom: 0;
}

.u-pos-center {
  @include abs-centering;
}

.u-pos-topleft {
  position: absolute;
  left: 0;
  top: 0;
}

.u-pos-fixed-white-bottom {
  @include fixed-bottom;
  background-color: $global-white;
}


// Floating and clearfix

.u-float-right {
  float: right;
}

.u-float-left {
  float: left;
}

.u-cf {
  @include inuit-clearfix();
}