/* ==========================================================================
   #SEARCH-BAR
   ========================================================================== */

/**
 * 1. Keep the search input width always equals to its container.
 */

.c-searchbar {
  margin-top: $unit/2;

  @include mq($from: desktop) {
    margin-top: 0;
  }

  [type="search"] {
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    border: 1px solid $global-border-color;
    width: 100%;
    @include font-size($global-small-type);

    @include mq($until: desktop) {
      padding: $unit/2;
    }
  }

  /**
  * 1. Clear the additional space made by default padding and border.
  */
  [type="submit"] {
    text-indent: -500px;

    /*[1]*/
    padding: 0;
    border: none;
  }
}

.c-searchbar__button {
  position: absolute;
  right: 0;

  .fa-search {
    color: $global-darkblue;
  }
}

.c-searchbar__button-icon {
  padding-right: $unit/2;
}

.c-searchbar__box {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;


  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}