/* ==========================================================================
   #HELPER FUNCTIONS
   ========================================================================== */

/** 
* Simply generate classes with an object (modifier: value),
* a subject ("txt", "border", etc),
* a prefix (component prefix "u", "c", "t", etc) and 
* a css property name.
*
* For a practical example, see "/utilities/_utilities.text.scss" 
*/

@mixin generate-class($modifier-value, $subject, $prefix, $property) {

  @each $modifier,
  $value in $modifier-value {

    .#{$prefix}-#{$subject}--#{$modifier} {
      #{$property}: $value;
    }
  }
}

/**
* Remove the unit of a length
*
* @param {Number} $number - Number to remove unit from
* @return {Number} - Unitless number
*/

@function strip-unit($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }

  @return $number;
}