/* ==========================================================================
   #WIDTH
   ========================================================================== */




$widths: ("10": 10%,
  "15": 15%,
  "20": 20%,
  "30": 30%,
  "33": 1/3*100%,
  "50": 50%,
  "60": 60%,
  "66": 2/3*100%,
  "70": 70%,
  "80": 80%,
  "90": 90%,
  "100": 100%,
);

$max-widths: ("70": 70%,
  "25": 25%,
  "30": 30%,
);

@include generate-class($widths, w, u, width);
@include generate-class($max-widths, mw, u, max-width);