/* ==========================================================================
   #REACTJS FIX
   ========================================================================== */

/* Modal */

.ReactModal__Body--open {
  overflow: hidden;
  position: fixed;
  width: 100%;
  height: 100%;

  .o-col--scrollable {
    overflow: hidden;
  }
}

.ReactMenu__Content,
.ReactCart__Content {
  width: 0;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}

.ReactMenu__Content {
  &.ReactModal__Content--after-open {
    width: 80%;
  }
}

.ReactCart__Content {
  &.ReactModal__Content--after-open {
    width: 100%;
  }
}

.ReactModal-Wrapper {
  padding: $unit;
}

.ReactModal-Topbar {
  height: $unit;
  background-color: $global-darkblue;
}

.ReactMenu-Item {
  padding-top: 18px;
  border-top: $global-border-style;
}

.ReactCart__Product {
  border-top: $global-border-style;
}


/* Button */
[type="button"] {
  border: none;
  background: transparent;
}