/* ==========================================================================
   #SLIDER
   ========================================================================== */




.c-slider {

  position: relative;

  /* Slider with the dots control on the top right corner */
  &.c-slider--right-dots {
    position: unset;
  }


  /********************
   Slick override styles
   ********************/

  .slick-dots {
    bottom: 12px;

    // Dots button font size
    li button {
      padding: 0;

      &:before {
        @include font-size($global-small-type, 1);
      }
    }
  }

  // Remove the default bottom margin of slick

  .slick-dotted {
    &.slick-slider {
      margin-bottom: 0;
    }
  }

  .slick-arrow {
    position: absolute;
    top: 0;
    z-index: $global-between-z-index;
    cursor: pointer;
    border: none;
    background: transparent;
    color: $global-darkblue;
    padding: 0;
    top: 50%;

    /* Override the default arrow text of slick */
    &:before {
      content: '';
    }

    @include mq(desktop) {
      i.medium {
        @include font-size(24px);
      }
    }

    &.right {
      @include mq($until: desktop) {
        margin-right: $unit;
      }

      right: 0;
    }

    &.left {
      @include mq($until: desktop) {
        margin-left: $unit;
      }

      left: 0;
    }
  }

  /********************/
}




/* Slick slider normalization */

.slick-dots {

  // Dots list margin

  [role="presentation"] {
    margin-left: 12px;
    margin-right: 0;

    &:first-child {
      margin-left: 0;
    }
  }
}

.slick-slide {
  margin-left: 0;
  outline: none;

  &:focus {
    outline: none;
  }

  .slick-list {
    margin-left: 0;
  }
}




// Slider syncing

.c-slider__nav {

  .slick-track {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  /*reactjs fix*/
  .c-slider__item {
    height: inherit !important;

    >img {
      margin: 0 auto;
      max-height: 84px;
      width: auto;
    }
  }
}

.c-slider__nav-item {
  height: inherit !important;

  >img {
    margin: 0 auto;
    max-height: 84px;
    width: auto;
  }
}

.c-slider__screen {

  .slick-track {
    margin-bottom: $unit;
  }

  /*reactjs fix*/
  .c-slider__item {
    img {
      margin: 0 auto;
      height: 255px;
    }
  }
}

.c-slider__screen-item {
  img {
    height: 255px;
  }
}




// Gutter size variations

.c-slider--tiny-gut {

  .slick-slide {
    padding-left: $unit/2;

    @include mq($from: desktop) {
      padding-left: $unit/4;
    }
  }

  .slick-list {
    margin-left: -$unit/2;

    @include mq($from: desktop) {
      margin-left: -$unit/4;
    }
  }
}

.c-slider--small-gut {

  .slick-slide {
    padding-left: $unit/4;

    @include mq($from: desktop) {
      padding-left: $unit/2;
    }
  }

  .slick-list {
    margin-left: -$unit/4;

    @include mq($from: desktop) {
      margin-left: -$unit/2;
    }
  }
}

.c-slider--large-gut {

  .slick-slide {

    @include mq($from: mobile) {
      margin-right: $unit;
    }

    .slick-list {
      @include mq($from: mobile) {
        margin-right: -$unit;
      }
    }
  }
}




// Layout style variations

.c-slider--same-h {

  .slick-track {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  .slick-slide {
    height: inherit;
  }

  img {
    height: 100%;
  }
}

.c-slider--w-img {

  img {
    // secure the image to be in the middle position of the slide
    margin: 0 auto;
  }
}